* {
  border: none;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

a, a:link, a:visited, a:hover {
  text-decoration: none;
}

:root {
  --text-primary: #000;
  --bg-primary: #fff;
  --bg-secondary: #f5f5f5;
  --header-item-bg: #fff6;
  --table-th-bg: #d9d9d9;
  --table-border: #dedede;
  --link-color: #1a76cb;
  --green-button-bg: #239050;
}

@media (prefers-color-scheme: dark) {
  :root {
    --text-primary: #fff;
    --bg-primary: #253137;
    --bg-secondary: #1a1a1a;
    --header-item-bg: #0006;
    --table-th-bg: #262626;
    --table-border: #212121;
    --link-color: #3385d1;
    --green-button-bg: #239050;
  }
}

a {
  color: var(--link-color, #1a76cb);
  transition: opacity .3s;
}

a:hover {
  opacity: .7;
}

html {
  scroll-behavior: smooth;
  background-color: var(--bg-primary, #fff);
  color: var(--text-primary, #000);
  -webkit-text-size-adjust: none;
}

body {
  background-image: url("bg-top.f6402442.svg");
  background-repeat: no-repeat;
  background-position: left top right;
  background-size: contain;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

main {
  display: block;
}

.content-wrapper {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (max-width: 800px) {
  .content-wrapper {
    padding: 0 15px;
  }
}

.mb-15 {
  margin-bottom: 15px;
}

.mb-30 {
  margin-bottom: 30px;
}

.centered-text {
  text-align: center;
}

.header {
  height: 80px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  width: 119px;
  height: 50px;
  position: relative;
}

.header__logo__container {
  background: var(--header-item-bg, #fff6);
  border-radius: 6px;
  padding: 5px;
}

.navigation ul, .navigation ul li {
  list-style: none;
}

.navigation ul {
  display: flex;
}

.navigation ul li {
  margin-right: 20px;
}

.navigation ul li:last-child {
  margin-right: 0;
}

.navigation ul li a {
  color: var(--text-primary, #000);
  background: var(--header-item-bg, #fff6);
  border-radius: 6px;
  padding: 5px;
  font-size: 18px;
  transition: opacity .3s;
}

.navigation ul li a:hover {
  opacity: .7;
}

@media (max-width: 800px) {
  .header__logo {
    width: 95px;
    height: 40px;
  }

  .navigation ul li {
    margin-right: 10px;
  }

  .navigation ul li a {
    font-size: 16px;
  }
}

.into-section {
  height: calc(100vh - 60px);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.into-section__title {
  font-size: 40px;
}

.info-section__subtitle {
  font-size: 18px;
}

.group-section {
  padding: 40px 0;
}

.group-section__title {
  margin-bottom: 20px;
  font-size: 30px;
}

.group-section__description {
  margin-bottom: 10px;
  font-size: 16px;
}

.group-section__content > * {
  margin-bottom: 30px;
}

.group-section__content > :last-child {
  margin-bottom: 0;
}

.section-title-button-row {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.table-wrapper {
  max-width: 1024px;
  width: 100%;
  -webkit-overflow-scrolling: touch;
  margin: 0 auto;
  display: flex;
  overflow-x: scroll;
}

.table-wrapper__scroll-padding_left {
  padding-left: 20px;
}

.table-wrapper__scroll-padding_right {
  padding-right: 20px;
}

.table-wrapper::-webkit-scrollbar, .table-wrapper::-webkit-scrollbar-thumb, .table-wrapper::-webkit-scrollbar-track {
  width: 0 !important;
  height: 0 !important;
  background: none !important;
  display: none !important;
}

@media (max-width: 800px) {
  .table-wrapper__scroll-padding_left {
    padding-left: 15px;
  }

  .table-wrapper__scroll-padding_right {
    padding-right: 15px;
  }
}

.table {
  width: 100%;
  border-collapse: collapse;
  background: var(--bg-primary, #fff);
  border: none;
  margin-bottom: 20px;
}

.table thead th {
  text-align: left;
  background: var(--table-th-bg, #d9d9d9);
  border: none;
  border-left: 1px solid var(--table-border, #dedede);
  border-right: 1px solid var(--table-border, #dedede);
  padding: 10px 15px;
  font-size: 14px;
  font-weight: bold;
}

.table tbody td {
  text-align: left;
  border: 1px solid var(--table-border, #dedede);
  vertical-align: middle;
  padding: 10px 15px;
  font-size: 14px;
}

.table-placeholder {
  height: 400px;
  background-color: var(--bg-secondary, #f5f5f5);
  border-radius: 12px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  font-size: 16px;
  display: flex;
  position: relative;
}

.lds-dual-ring {
  width: 80px;
  height: 80px;
  display: inline-block;
}

.lds-dual-ring:after {
  content: " ";
  width: 64px;
  height: 64px;
  border: 6px solid var(--text-primary, #000);
  border-color: var(--text-primary, #000) transparent var(--text-primary, #000) transparent;
  border-radius: 50%;
  margin: 8px;
  animation: 1.2s linear infinite lds-dual-ring;
  display: block;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.green-button {
  background: var(--green-button-bg, #239050);
  color: #fff;
  border-radius: 6px;
  padding: 12px;
  font-size: 16px;
  font-weight: bold;
  transition: opacity .3s;
}

.green-button:hover {
  opacity: .8;
}

.gray-button {
  background: var(--table-border, #dedede);
  cursor: pointer;
  border-radius: 6px;
  padding: 12px;
  font-size: 16px;
  line-height: 0;
  transition: opacity .3s;
}

.gray-button:hover {
  opacity: .8;
}

.gray-button:disabled {
  opacity: .7;
}

.footer {
  min-height: 200px;
  background-image: url("footer-bg-2.e64a82dd.svg");
  background-position: 100% 100%;
  background-repeat: no-repeat;
  background-size: contain;
  padding: 40px 15px;
}

.footer__logo {
  height: 100px;
}

.footer__content {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.footer__content__text > * {
  margin-bottom: 10px;
}

.footer__content__text > :last-child {
  margin-bottom: 0;
}

@media (max-width: 800px) {
  .footer__content {
    text-align: center;
    flex-direction: column;
  }

  .footer__content__text > * {
    margin-bottom: 15px;
  }

  .footer__logo {
    margin-bottom: 40px;
  }
}

#group-a {
  background-image: url("group-a-bottom-bg.f69cbcce.svg");
  background-position: 0 100%;
  background-repeat: no-repeat;
  background-size: 60%;
  padding-bottom: 50px;
}

#group-b {
  background-image: url("group-b-top-bg-3.165a818d.svg");
  background-position: 100% 0;
  background-repeat: no-repeat;
  background-size: 60%;
  padding-top: 50px;
}

@media (max-width: 800px) {
  #group-a {
    background-size: 220%;
  }

  #group-b {
    background-size: 180%;
  }
}

